/* ICON BAR */
#icon-bar {
  list-style-type: none;
  text-align: center;
  display: inline-block;
}

.icon-button {
  background-color: #ffffff;

  border: none;
  color: white;
  cursor: pointer;
  border-radius: 50%;
  width: 5em;
  height: 5em;
  transition-duration: 0.2s;
  float: left;
  margin: 1vh 1vw;
}

.icon-button img {
  width: 40px;
  height: 40px;
  margin-top: 1.6em;
}

.icon-button:hover {
  /* background-color: #387759; */
  /* background-color: #476b49; */
  /* background-color: #845c83; */
  background-color: #593b61;
}

/* DOWN SCROLL ARROW BUTTON, based off of and modified from https://codepen.io/nxworld/pen/OyRrGy */
section {
  position: relative;
}

#down-arrow a:hover {
  opacity: 0.5;
  box-shadow: 0.5em;
  cursor: pointer;
}

/* #down-arrow a { */
#down-arrow div {
  margin-top: 10vh;
  padding-top: 60px;
  display: inline-block;
  transform: translate(0, 0);
  color: #fff;
  letter-spacing: 0.1em;
  text-decoration: none;
  transition: opacity 0.3s;
}

/* #down-arrow a span { */
#down-arrow div span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: rotate(-45deg);
  -webkit-animation: scrolldownbutton 2s infinite;
          animation: scrolldownbutton 2s infinite;
}

/* #down-arrow a span:nth-of-type(1) { */
#down-arrow div span:nth-of-type(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}

/* #down-arrow a span:nth-of-type(2) { */
#down-arrow div span:nth-of-type(2) {
  top: 16px;
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
}

@-webkit-keyframes scrolldownbutton {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes scrolldownbutton {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

