#navbar {
  overflow: hidden;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  list-style-type: none;
  background-color: #4e8050;
  width: 100%;
  display: flex;
  justify-content: center;
}

#navbar a {
  float: left;
  /* display: block; */
  color: #f2f2f2;
  /* Change padding for more space in navbar */
  padding: 1em 3em;
  font-size: 1.7rem;
  text-decoration: none;
  background-size: 100% 200%;
  /* background-image: linear-gradient(to top, #476b49 50%, #749975 50%); */
  background-image: linear-gradient(to top, #845c83 50%, #4e8050 50%);
  transition: background-position 0.4s;
  border-bottom: 5px solid #845c83;
}

#navbar a:hover {
  background-position: 0 100%;
}

